/* base */
body {
  font-family: Roboto,"Noto Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", 
     "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
  /* mac, IOS 系統字體 設計稿中文字體 設計稿英文字體  Windows 英文系統字  微軟正黑體 Android 系統字  IOS 系統字  通用字體 */

  font-size: 16px;
  line-height: 1.5;
}
a {
  &:hover {
    opacity: 0.7;
  }
}
